html, body {
  height: 100%;
  line-height: 25px; }

body {
  color: #090B13;
  font-family: filson-pro; }

h1 {
  font-weight: 700;
  font-size: 70px;
  line-height: 80px; }

h2 {
  font-weight: 500;
  font-size: 40px; }

h4 {
  font-weight: 300;
  font-size: 20px; }

p {
  font-weight: 300;
  font-size: 18px;
  width: 400px;
  margin-bottom: 30px; }
  p > span {
    font-weight: 500;
    color: #3A71D9; }

section {
  display: flex; }
  section:nth-of-type(even) {
    background: #F8F8FA; }

::selection {
  color: white;
  background: #3A71D9;
  /* WebKit/Blink Browsers */ }

::-moz-selection {
  color: white;
  background: #3A71D9;
  /* Gecko Browsers */ }
